import Table from '../../mo/models';

export default function isEmptyTable(table: Table) {
  const rowSize = table.nodes.length;
  for (let i = 0; i < rowSize; i += 1) {
    const row = table.nodes[i];
    if (row.type !== 'table-row') {
      continue; // eslint-disable-line no-continue
    }
    const cells = row.nodes.filter(({ type }) => type === 'table-cell');
    if (cells.length !== 0) {
      return false;
    }
  }
  return true;
}
